import nacl from 'tweetnacl';
import naclutil from 'tweetnacl-util';

import { EventEmitter, isBrowser, listify } from '../utils';

nacl.util = naclutil;

const nullNonce = new Uint8Array(new ArrayBuffer(24), 0);

import RpcClient from '../rpc/client';

import RPCTarget from '../rpc/RPCTarget';

function isObject(obj) {
  return obj !== undefined && obj !== null && obj.constructor == Object;
}

// SERVER-SIDE CHANNEL
// CLIENT-SIDE is called "CONNECTOR"
class Channel extends EventEmitter {
  constructor(ws, { verbose }) {
    super();
    this.ws = ws;
    this.verbose = verbose;

    this.protocol = ws.protocol;

    this.reverseRpcClient = new RpcClient(this);

    this.sentMessageCount = 0;
    this.receivedMessageCount = 0;

    ws.on('message', msg => {
      this.receivedMessageCount += 1;
    });

    ws.on('close', () => {
      this.emit('channel_closed');
    });

    // todo -- get this back
    // if (dmt.isDevMachine() && !this.remoteIp) {
    //   log.red('WARNING: cannot read websocket remote Ip!! -- TODO: debug... happens mostly on lan');
    // }
  }

  setProtocolLane(protocolLane) {
    this.protocolLane = protocolLane;
  }

  setSharedSecret(sharedSecret) {
    this.sharedSecret = sharedSecret;
  }

  setClientInitData(clientInitData) {
    this._clientInitData = clientInitData;
  }

  clientInitData() {
    return this._clientInitData;
  }

  remoteIp() {
    return this.ws.remoteIp;
  }

  setRemotePubkey(remotePubkeyHex) {
    this.remotePubkeyHex = remotePubkeyHex;
  }

  remotePubkey() {
    return this.remotePubkeyHex;
  }

  // reverse RPC call from server to client
  remoteObject(handle) {
    return {
      call: (methodName, params = []) => {
        return this.reverseRpcClient.remoteObject(handle).call(methodName, listify(params)); // rpcClient always expects a list of arguments, never a single argument
      }
    };
  }

  streamFile({ filePath, sessionId }) {
    if (isBrowser()) {
      throw new Error('Cannot stream file from browser, use this only from node.js process!');
    } else {
      import('../fileTransport/feedBytesIntoChannel/streamFile').then(streamFileModule => streamFileModule.default({ filePath, sessionId, channel: this }));
    }
  }

  // used after we detect that connection is not authorized after a few first messages
  terminate() {
    this.ws.terminated = true;

    this.ws.close();

    process.nextTick(() => {
      if ([this.ws.OPEN, this.ws.CLOSING].includes(this.ws.readyState)) {
        // Socket still hangs, hard close
        this.ws.terminate();
      }
    });
  }

  terminated() {
    return this.ws.terminated;
  }

  closed() {
    return [this.ws.CLOSED, this.ws.CLOSING].includes(this.ws.readyState);
  }

  // todo: detect if handle is already taken ....
  registerRemoteObject(handle, obj) {
    new RPCTarget({ serversideChannel: this, serverMethods: obj, methodPrefix: handle });
  }

  messageReceived(message) {
    // console.log('BEFORE DECRYPTION:');
    // console.log(message);
    if (this.sharedSecret) {
      //console.log('decrypting ....');

      if (this.verbose == 'extra') {
        console.log('Received bytes:');
        console.log(message);
        console.log(`Decrypting with shared secret ${this.sharedSecret}...`);
      }

      try {
        const _decryptedMessage = nacl.secretbox.open(message, nullNonce, this.sharedSecret);
        const flag = _decryptedMessage[0]; // should always be 1 (string) here because channel does not receive binary data, only connector (for now ?)
        const decryptedMessage = _decryptedMessage.subarray(1);

        const decodedMessage = nacl.util.encodeUTF8(decryptedMessage);
        message = decodedMessage;
      } catch (e) {
        throw new Error(`${message} -- ${this.protocol} -- ${e.toString()}`);
      }
    }

    let jsonData;

    try {
      jsonData = JSON.parse(message);
    } catch (e) {
      console.log('---');
      console.log(message);
      console.log('---');
      return;
    }

    if (this.verbose) {
      if (this.sharedSecret) {
        console.log('Decrypted message:');
      } else {
        console.log('Received message:');
      }
      console.log(message);
      console.log();
    }

    if (jsonData.jsonrpc) {
      // if we received rpc message with result or error property on the server, it means
      // that this is a result of a reverse rpc call (from server to client) and now result from client is coming back on our
      // (serverside) channel
      if (Object.keys(jsonData).includes('result') || Object.keys(jsonData).includes('error')) {
        this.reverseRpcClient.jsonrpcMsgReceive(message);
      } else {
        // todo: remove
        // console.log(`Received RPC call from client:`);
        // console.log(jsonData);
        // if we received a call without result or error property on server, that means it is a normal RPC call
        this.emit('json_rpc', message);
      }
    } else if (jsonData.tag == 'request_file') {
      this.streamFile(jsonData); // { filePath, sessionId }
    } else {
      this.emit('message', message);
    }
    // } catch (e) {
    //   // todo: remove! after this error is found:

    //   //      lab pid 7123 3/17/2020, 6:27:05 PM 39915ms (+236ms) âˆž mpv engine play /root/DMTMountedMedia/solar/Music/Categories/Progressive/Shadow Gallery/Shadow Gallery - 2007 - Prime Cuts/Shadow Gallery - 07. The Final Hour.mp3
    //   //lab pid 7123 3/17/2020, 6:29:16 PM 170934ms âˆž Caught global exception: SyntaxError: Unexpected token ` in JSON at position 0
    //   //lab pid 7123 3/17/2020, 6:29:16 PM 170945ms (+11ms) âˆž SyntaxError: Unexpected token ` in JSON at position 0
    //   //    at JSON.parse (<anonymous>)
    //   //at Channel.messageReceived (file:///root/.dmt/core/node/connectome/lib/wsChannel/channel.js:94:27)
    //   //at WebSocket.<anonymous> (file:///root/.dmt/core/node/connectome/lib/wsServer/serverInstance.js:103:19)
    //   //at WebSocket.emit (events.js:333:22)
    //   //at Receiver.receiverOnMessage (/root/.dmt/core/node/connectome/node_modules/ws/lib/websocket.js:801:20)
    //   //at Receiver.emit (events.js:321:20)
    //   //at Receiver.dataMessage (/root/.dmt/core/node/connectome/node_modules/ws/lib/receiver.js:427:14)
    //   //at Receiver.getData (/root/.dmt/core/node/connectome/node_modules/ws/lib/receiver.js:366:17)
    //   //at Receiver.startLoop (/root/.dmt/core/node/connectome/node_modules/ws/lib/receiver.js:142:22)
    //   //at Receiver._write (/root/.dmt/core/node/connectome/node_modules/ws/lib/receiver.js:77:10)
    //   //lab pid 7123 3/17/2020, 6:29:16 PM 170947ms (+02ms) âˆž EXITING, bye âœ‹

    //   throw new Error(`${message} ${e.toString()}`);
    // }
  }

  // warning: duplication in Connector
  addHeader(_msg, flag) {
    const msg = new Uint8Array(_msg.length + 1);

    const header = new Uint8Array(1);
    header[0] = flag;

    // concat!
    msg.set(header);
    msg.set(_msg, header.length);

    return msg;
  }

  send(message) {
    if (isObject(message)) {
      message = JSON.stringify(message);
    }

    if (this.verbose) {
      if (this.sharedSecret) {
        console.log('Sending encrypted message:');
      } else {
        console.log('Sending message:');
      }

      console.log(message);
    }

    // TODO
    if (this.sharedSecret) {
      let flag = 0; // binary

      if (typeof message == 'string') {
        flag = 1; // string
      }

      const _encodedMessage = flag == 1 ? nacl.util.decodeUTF8(message) : message; // binary data (file stream...)
      const encodedMessage = this.addHeader(_encodedMessage, flag);

      const encryptedMessage = nacl.secretbox(encodedMessage, nullNonce, this.sharedSecret);
      message = encryptedMessage;

      if (this.verbose == 'extra') {
        console.log('Encrypted bytes:');
        console.log(encryptedMessage);
      }
    }

    if (this.verbose) {
      console.log();
    }

    //if (!this.terminated) {
    if (!this.ws.terminated && this.ws.readyState == this.ws.OPEN) {
      this.sentMessageCount += 1;
      this.ws.send(message);
    } else {
      this.ws.terminated = true;
      // TODO: improve and let caller know the message couldn't be sent! and let it deal with it
      //log.red(`tried to send over closed channel: ${message}`);
    }
    //}
  }
}

export default Channel;
